<template>
  <div class="Category">
    <HeaderInfo headerInfo="" />
    <div class="m-category-list">
      <router-link :to="category.link" v-for="(category, index) in categoryData">
        <div class="m-category">
          {{ category.name }}
        </div>
      </router-link>
    </div>
    <ModalSetting />
    <Tabs />
  </div>
</template>

<script>
  import HeaderInfo from '../components/Header/HeaderInfo'
  import Tabs from '../components/Tabs/Tabs'
  import ModalSetting from '../components/Modal/ModalSetting.vue'

  export default {
    components: {
      'HeaderInfo': HeaderInfo,
      'Tabs': Tabs,
      'ModalSetting': ModalSetting
    },
    data () {
      return {
        categoryData: [
          {
            name: '  ',
            link: '/list/  '
          },
          {
            name: '',
            link: '/list/'
          },
          {
            name: '',
            link: '/list/'
          },
          {
            name: 'TV',
            link: '/list/TV'
          },
          {
            name: '',
            link: '/list/'
          },
          {
            name: '',
            link: '/list/'
          },
          {
            name: '',
            link: '/list/'
          },
          {
            name: '',
            link: '/list/'
          },
          {
            name: '',
            link: '/list/'
          },
          {
            name: '',
            link: '/list/'
          },
          {
            name: '',
            link: '/list/'
          },
          {
            name: '',
            link: '/list/'
          },
          {
            name: '',
            link: '/list/'
          },
          {
            name: '',
            link: '/list/'
          }
        ]
      }
    }
  }
</script>

<style>
  .Category {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    bottom: 50px;
    overflow-y: auto;
  }

  .m-category-list h3 {
    color: #4374D9;
  }

  .m-category {
    font-weight: bold;
    height: 35px;
    padding-top: 15px;
    border-bottom: 1px solid #5D5D5D;
    /* border-top: 0.5px solid #fff; */
    /* margin: 8px 6px; */
    /* background-color: #3A3A3A; */
  }

  @media screen and (max-width: 700px) {
    .Category {
      padding-top: 43px;
    }
  }
</style>



