<template>
  <div class="YoutubeList">
    <Header />
    <YoutubeMenu :categoryList="categoryList" />
    <YoutubeMain :categoryList="categoryList" />
    <Tabs />
    <DataLoading />
    <ModalSetting />
  </div>
</template>

<script>
  import Header from '../components/Header/Header'
  import YoutubeMenu from '../components/YoutubeList/YoutubeMenu/YoutubeMenu'
  import YoutubeMain from '../components/YoutubeList/YoutubeMain/YoutubeMain'
  import Tabs from '../components/Tabs/Tabs'
  import DataLoading from '../components/common/DataLoading'
  import ModalSetting from '../components/Modal/ModalSetting.vue'

  export default {
    components: {
      'Header': Header,
      'YoutubeMenu': YoutubeMenu,
      'YoutubeMain': YoutubeMain,
      'DataLoading': DataLoading,
      'Tabs': Tabs,
      'ModalSetting': ModalSetting
    },
    data () {
      return {
        categoryList: [
          {
            name: '',
            path: '/list'
          },
          {
            name: '  ',
            path: '/list/  '
          },
          {
            name: '',
            path: '/list/'
          },
          {
            name: '',
            path: '/list/'
          },
          {
            name: 'TV',
            path: '/list/TV'
          },
          {
            name: '',
            path: '/list/'
          },
          {
            name: '',
            path: '/list/'
          },
          {
            name: '',
            path: '/list/'
          },
          {
            name: '',
            path: '/list/'
          },
          {
            name: '',
            path: '/list/'
          },
          {
            name: '/',
            path: '/list/'
          },
          {
            name: '',
            path: '/list/'
          },
          {
            name: '',
            path: '/list/'
          },
          {
            name: '',
            path: '/list/'
          },
          {
            name: '',
            path: '/list/'
          }
        ]
      }
    }
  }
</script>

<style>
.YoutubeList {
  overflow: hidden
}
</style>



